<template>
  <div id="login_div">
    <el-row :gutter="12">
      <el-col :span="8" :offset="8">
        <el-card shadow="always">
          <div v-if="formMode==1" id="login_card">
            <img v-if="!isLoading" src="@/assets/KiwriousLogo_500x500.png">
            <img v-if="isLoading" src="@/assets/LoadingAnimation2.gif">
            <p><b>Username or email address</b></p>
            <el-input v-model="username"></el-input>
            <p><b>Password</b></p>
            <el-input v-model="password" show-password></el-input>
            <!-- <div class="error"><span>{{password_message}}</span></div> -->
            <button @click="signIn()" :disabled="isLoading" class="k-button-1 login-button">Sign in</button>
            <!-- <el-button class="k-buttton-1" round @click="check">Sign in</el-button> -->
            <!-- <p class="switch-button" v-show="!isLoading" @click="changeForm(2)">New to Kiwrious? create an account</p> -->
          </div>
          <div v-if="formMode==2" id="login_card">
            <img v-if="!isLoading" src="@/assets/KiwriousLogo_500x500.png">
            <img v-if="isLoading" src="@/assets/LoadingAnimation2.gif">
            <p><b>Username</b></p>
            <el-input v-model="username"></el-input>
            <p><b>Email</b></p>
            <el-input v-model="email"></el-input>
            <p><b>Full name</b></p>
            <el-input v-model="name"></el-input>
            <p><b>Organization</b></p>
            <el-input v-model="organization"></el-input>
            <p><b>Password</b></p>
            <el-input v-model="password" show-password></el-input>
            <p><b>Confirm Password</b></p>
            <el-input v-model="confirmation" show-password></el-input>
            <!-- <div class="error"><span>{{password_message}}</span></div> -->
            <button @click="register()" :disabled="isLoading" class="k-button-1 login-button">Register</button>
            <!-- <el-button class="k-buttton-1" round @click="check">Sign in</el-button> -->
            <p class="switch-button" v-show="!isLoading" @click="changeForm(1)">New to Kiwrious? create an account</p>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL
  })
  const config = {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
  export default {
    props:['id'],
    data() {
      return {
        username: '',
        password: '',
        confirmation: '',
        name:'',
        email:'',
        organization:'',
        password_message: '',
        isLoading: false,
        formMode: 1
      }
    },
    methods: {
      signIn() {
        if (!this.username || !this.password) {
          this.$message({message: 'Please enter username and password',type: 'error'}); return;
        } 
        var userInfo = {loginId: this.username}
        const url = '/auth/SignIn';
        this.isLoading = true;
        api.post(url, qs.stringify(userInfo),config)
        .then((response) => {
          if (response.status === 200) {
            if (this.password == response.data.Password) {
              this.$store.dispatch('setRoleData', response.data.Role)
              this.$store.dispatch('setUidData', response.data.Id)
              this.$store.dispatch('setOidData', response.data.OrganizationId)
              this.$store.dispatch('setNameData', response.data.Name)
              this.$store.dispatch('setAchivementsData', response.data.achivements)
              if(this.id){
                this.$router.push({path: '/finding/'+this.id});
              } else {
                this.$router.push({path: '/home/myinquiries'})
              }
            } else {
              this.isLoading = false;
              this.$message({
                message: 'Wrong Password',
                type: 'error'
              })
            }
          }
          console.log(response)
        })
        .catch((error) => {
          this.isLoading = false;
          if (error.message == 'Request failed with status code 404') {
            const e = 'Please check your username'
            this.$message({
              message: e,
              type: 'error'
            })
          }
          console.log(error.message)
        })     
      },
      register(){
        if (this.password != this.confirmation) {
          this.$message({message: "Password confirmation didn't match",type: 'error'});return;
        } 
        if(!this.username){this.$message({message:"Username cannot be empty", type: 'error'});return;}
        if(!this.email){this.$message({message:"Email cannot be empty", type: 'error'});return;}
        if(!this.name){this.$message({message:"Name cannot be empty", type: 'error'});return;}
        if(!this.organization){this.$message({message:"Name cannot be empty", type: 'error'});return;}
        if(!this.password){this.$message({message:"Password cannot be empty", type: 'error'});return;}
        var newUser = {name: this.name, roleName: 'Student', email: this.email, password: this.password, nickname: this.username}
        const url = '/user/';
        this.isLoading = true;
        api.post(url, qs.stringify(newUser),config).then((response)=>{
          this.isLoading = false;
          this.$message({message: "Registration completed successfully",type: 'success'});
          var un = this.username;
          this.changeForm(1);
          this.username = un;
        }).catch((error)=>{
          this.isLoading = false;
          this.$message({
            message: error.message,
            type: 'error'
          });
        });
      },
      changeForm(mode){
        this.username = '';
        this.password = '';
        this.confirmation = '';
        this.organization = '';
        this.name = '';
        this.email = '';
        this.formMode = mode;
      }
    },
  }
</script>

<style lang="scss">

  .login-button{
    margin-top: 20px !important;
    width: 70% !important;
  }
  #login_div {
    margin-top: 180px;
  }
  #login_card {
    text-align: center;
    p {
      margin: 0; 
    }
    img {
      height: 140px;
    }
    .el-input {
      font-size: 18pt;
      margin-bottom: 10px;
    }
    .el-input__inner {
      color: gray;
      width: 70%;
      background-color: white !important;
      border-width: 0px 0px 1px 0px;
      text-align: center;
    }
    // .el-button {
    //   margin-top: 20px;
    //   width: 50%;
    //   background-color: #178de7;
    //   color: #fff;
    // }
    // .el-button:hover{
    //   background-color: #1372ba;
    // }
    .error {
      color: red;
    }
    .switch-button{
      cursor: pointer;
      margin-top: 20px;
      color: gray;
      transition: color 0.5s;
    }
    .switch-button:hover{
      color: black;
    }
  }
</style>