<template>
  <div id="overview-wrapper">
    <div id="overview-content">
      <div id="overview-img">
        <button class="k-button-1" @click="editImg()">Edit</button>
        <img :src="overview_img">
        <!-- <img v-if="datasetIndex!=0" class="clickable" src="@/assets/left-arrow.png" @click="setDatasetIndex(-1)" style="position: absolute; bottom: 45%; left: 35px; width: 70px; height: 70px; border-radius: 50px;"/>
        <img v-if="datasetIndex!=finding.Dataset.length-1" class="clickable" src="@/assets/right-arrow.png" @click="setDatasetIndex(+1)" style="position: absolute; bottom: 45%; right: 35px; width: 70px; height: 70px; border-radius: 50px;"/> -->
        <div id="overview-arnote">
          <span>{{time}}</span>
          <span>{{overview_note}}</span>
        </div>  
      </div>
      <div id="overview-input">
        <el-input type="text" name="" v-model="ar_title" placeholder="Name your Inquiry" maxlength="50" show-word-limit></el-input>
        <el-input type="textarea" name="" v-model="ar_description"  placeholder="What interesting things did you find in this inquiry?" maxlength="200" show-word-limit></el-input>
      </div>
      <div id="overview-btn">
        <!-- <button id="overview-save">Save Draft</button> -->
        <button id="overview-publish" v-on:click="ar_pubcheck()">Publish</button>
      </div>
    </div>
  </div>
	
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL
  })
  export default {
    data() {
      return {
        ar_imgsrc: this.$store.state.arimg,
        overview_img: this.$store.state.arimg[0],
        ar_label: this.$store.state.arlabel,
        ar_note: this.$store.state.arnote,
        overview_note: this.$store.state.arnote[0],
        ar_re: this.$store.state.arre,
        time: this.$store.state.time,
        ar_title: null,
        ar_description: null,
        uv: this.$store.state.uv,
        lux: this.$store.state.lux,
      }
    },
    methods: {
      editImg() {
        this.$router.push({path: '/find_ar', query: {editIndex: 0}})
      },
      ar_pubcheck() {
        if (this.ar_title == null) {
          this.$message({
                message: 'Please input your project title',
                type: 'error'
              })
        } else {
          this.ar_publish()
        }
      },
      ar_publish() {
        this.$loading.show({delay:0})
        const url = '/finding';
        // const datasrc = '[{condition:"'+this.ar_label+'",uv:'+this.uv+',lux:'+this.lux+',media:"'+imgsrc+'"}]'
        const finding = {
          name: this.ar_title,
          description: this.ar_description,
          notes: this.ar_note[0],
          timeStamp: this.time,
          userId : sessionStorage.uid,
          inquiryId: '948a06f5-5f03-498a-9258-3f89ea1e2362',
          dataset: [],
          type: this.ar_re.type,
          base: this.ar_re.base,
          status: '2',
          similarity: '3',
          sensorId: 'uv_001',
        }
        let img_index = 0
        while(img_index < this.ar_imgsrc.length) {
          finding.dataset.push({
            condition: this.ar_label[img_index],
            uv: this.uv,
            lux: this.lux,
            notes: this.ar_note[img_index],
            media: this.ar_imgsrc[img_index].split(',')[1],
            observable: [{
              key: 'Uv',
              value: this.uv,
            },{
              key: 'Lux',
              value: this.lux,
            }]
          })
          img_index++
        }
        console.log(finding)
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.post(url, qs.stringify(finding),config).then((response)=>{
          console.log('send')
          this.$loading.hide()
          this.$router.push({path: '/home/myinquiries'})
        }).catch((e) => {
          console.log(e)
          this.$message({
                message: e,
                type: 'error'
              })
          this.$loading.hide()
        });
      }
    },
  }
</script>

<style lang="scss">
  #overview-wrapper {
    background-color: #F4F6F9;
    height: 100%;
  }
  #overview-content {
    margin: auto;
    width: 60%;
    background-color: white;
    height: 100%;
  }
  #overview-img {
    width: 640px;
    margin: auto;
    padding-top: 18px;
    border-radius: 5px;
    overflow: hidden;
  }
  #overview-img img {
    display: block;
  }
  #overview-img button {
    width: 110px;
    height: 40px;
    position: absolute;
    margin-left: 200px;
  }
  #overview-arnote {
    min-height: 88px;
    background-color: #E6EEEF;
    height: auto;
  }
  #overview-arnote span {
    text-align: left;
    display: block;
    padding-left: 22px;
    padding-top: 14px;
    font-size: 18px;
    min-width: 30px;
    max-width: 85%;
    word-wrap: break-word;
  }
  #overview-input {
    width: 640px;
    margin: auto;
    input {
      width: 99%;
      border: 2px solid #E0E0E0;
      border-radius: 5px;
      height: 36px;
      margin-top: 16px;
      font-size: 20px;
    }
    textarea {
      width: 99%;
      border: 2px solid #E0E0E0;
      border-radius: 5px;
      height: 80px;
      margin-top: 16px;
      font-size: 18px;
      font-family: 'Arial';
    }
  }
  #overview-btn {
    padding-top: 20px;
    width: 640px;
    margin: auto;
    #overview-save {
      width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: #FA5708;
      background-color: white;
      border: 2px solid #FDB18B;
      float: left;
    }
    #overview-publish {
      width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: white;
      background-color: #FA5708;
      border: 2px solid #FDB18B;
      float: right;
      cursor: pointer;
    }
  }
</style>