<template>
	<div class="finding_wrap">
    <div class="finding_content">
      <div class="finding-header" style="position: relative;">
        <i class="el-icon-user-solid"></i><label id="finding-username">{{finding.UserNickname}}</label>
        <div v-if="isOwnFinding()">
          <div v-if="!showPublishMenu" class="publish-button" @click="showPublishMenu = true;"> 
            <p>{{finding.Status==2? 'Published':'Draft'}}</p>
          </div>
          <div v-else class="publish-menu"> 
            <div @click="unpublish()"><img src="@/assets/visibility-off-24-px@2x.png"/><label>Unpublish</label></div>
            <div @click="copyLink()"><img src="@/assets/insert-link-24-px@2x.png"/><label>Copy Link</label></div>
            <div @click="inviteOthers()"><img src="@/assets/mail-24-px@2x.png"/><label>Invite to view</label></div>
            <div @click="deleteFinding()"><img src="@/assets/baseline_delete_forever_black_24dp.png"/><label>Delete</label></div>
          </div>
        </div>
      </div>
      <div class="finding_block">
        <div class="finding-title">
          <p id="finding-name">{{finding.Name}}</p>
          <p id="finding-description">{{finding.Description}}</p>
        </div>
        <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(finding)">
        <div class="notes-section">
          <p id="finding-time">{{finding.TimeStamp}}</p>
          <p id="finding-note">{{getSceneNotes()}}</p>
        </div>
        <img v-if="datasetIndex!=0" class="clickable left-arrow" src="@/assets/left-arrow.png" @click="setDatasetIndex(-1)"/>
        <img v-if="datasetIndex!=finding.Dataset.length-1" class="clickable right-arrow" src="@/assets/right-arrow.png" @click="setDatasetIndex(+1)"/>
      </div>
      <div id="finding_comment">
        <p>{{comments.length}} Comments</p>
        <div class="comment_row">
          <i style="color: black;" class="el-icon-user-solid"></i>
          <span class="comment-user">{{username}}</span><br><br>
          <img src="@/assets/add-24-px@2x.png" v-bind:class="{'selected-comment':commentType==1}" class="clickable" @click="enableCommentInput(1)"/>
          <el-tooltip content="Compliment" placement="top">
          <img src="@/assets/favorite-24-px@2x.png" v-bind:class="{'selected-comment':commentType==1, 'greyout':isCommentInputEnabled}" class="clickable" @click="enableCommentInput(1)"/>
          </el-tooltip>
          <el-tooltip content="Question" placement="top">
          <img src="@/assets/help-g-24-px@2x.png" v-bind:class="{'selected-comment':commentType==2, 'greyout':isCommentInputEnabled}" class="clickable" @click="enableCommentInput(2)"/>
          </el-tooltip>
          <el-tooltip content="Suggestion" placement="top">
          <img src="@/assets/wb-incandescent-24-px@2x.png" v-bind:class="{'selected-comment':commentType==3, 'greyout':isCommentInputEnabled}" class="clickable" @click="enableCommentInput(3)"/>
          </el-tooltip>
          <!-- <textarea type="text " v-if="isCommentInputEnabled" v-model="commentValue" maxlength="300"> </textarea> -->
          <el-input
            v-if="isCommentInputEnabled"
            type="textarea"
            autosize
            placeholder="Type your comment here..."
            v-model="commentValue"
            maxlength="300"
            show-word-limit
          >

          </el-input>
          <img v-show="isCommentInputEnabled"  src="@/assets/baseline_check_black_24dp.png" class="clickable mini-button" @click="postComment()"/>
          <img v-if="isCommentInputEnabled" src="@/assets/baseline_close_black_24dp.png" class="clickable mini-button" @click="isCommentInputEnabled=false; commentType=0; commentValue=''"/>
          <!-- <div style="display: inline-block; ">
             <label v-if="isCommentInputEnabled" style="vertical-align: middle; font-size: 2.5vh; color: lightgray;">{{commentValue.length}}/300</label>
          </div> -->
        </div>
        <div class="comment_row" v-for="comment in comments" v-bind:key="comment.Id">
          <i style="color: black;" class="el-icon-user-solid"></i>
          <span class="comment-user">{{comment.UserName}}</span>
          <br><br>
          <img v-if="selectedComment!=comment && comment.Type==1" src="@/assets/favorite-24-px@2x.png" />
          <img v-if="selectedComment!=comment && comment.Type==2" src="@/assets/help-g-24-px@2x.png"/>
          <img v-if="selectedComment!=comment && comment.Type==3" src="@/assets/wb-incandescent-24-px@2x.png"/>
          
          <div style="display: inline; height: 100%; vertical-align: top; margin-right: 5px; padding-top: 5px !important;"><label v-if="selectedComment!=comment" class="comment-value">{{comment.Value}}</label></div>

          <img v-if="selectedComment==comment" src="@/assets/favorite-24-px@2x.png" v-bind:class="{'selected-comment':comment.Type==1, 'greyout':comment.Type!=1}" class="clickable" @click="comment.Type=1"/>
          <img v-if="selectedComment==comment" src="@/assets/help-g-24-px@2x.png" v-bind:class="{'selected-comment':comment.Type==2, 'greyout':comment.Type!=2}" class="clickable" @click="comment.Type=2"/>
          <img v-if="selectedComment==comment" src="@/assets/wb-incandescent-24-px@2x.png" v-bind:class="{'selected-comment':comment.Type==3, 'greyout':comment.Type!=3}" class="clickable" @click="comment.Type=3"/>
          
           <el-input
            v-if="selectedComment==comment"
            type="textarea"
            autosize
            placeholder="Type your comment here..."
            v-model="comment.Value"
            maxlength="300"
            show-word-limit
          >
          </el-input>
          <!-- <textarea type="text " v-if="selectedComment==comment" v-model="comment.Value" maxlength="300"></textarea> -->

          <img v-if="comment.UserId==userId && !selectedComment" class="clickable mini-button" @click="deleteComment(comment.Id)" src="@/assets/baseline_delete_forever_black_24dp.png"/>
          <img v-if="comment.UserId==userId && !selectedComment" class="clickable mini-button" @click="editComment(comment)" src="@/assets/baseline_edit_black_24dp.png"/>

          <img v-if="selectedComment==comment" @click="saveComment(comment)" class="clickable mini-button" src="@/assets/baseline_check_black_24dp.png"/>
          <img v-if="selectedComment==comment" @click="revertComment(comment)" class="clickable mini-button" src="@/assets/baseline_close_black_24dp.png"/>
          <!-- <div  style="display: inline-block; ">
             <label v-if="selectedComment==comment" style="vertical-align: middle; font-size: 2.5vh; color: lightgray;">{{comment.Value.length}}/100</label>
          </div> -->
        </div>
      </div>
    </div>

    <div class="middle-column"></div>

    <div class="finding-list">

      <div id="original-finding-reference" v-if="isRemix()">
        <div>
          <label id="ofr-1">This is a remix of </label><br>
          <label id="ofr-2" @click="loadFinding(parentFinding.Id)" >{{parentFinding.Name}}</label>
        </div>
      </div>

      <div id="replication-remix-tabs" v-if="isOriginal() || isRemix()">
        <button class="replication-remix-tab" @click="selectedTab=1;" v-bind:class="{'selected-tab':selectedTab==1}">
          {{replications.length}} Replications
        </button>
        <button class="replication-remix-tab" @click="selectedTab=2;" v-bind:class="{'selected-tab':selectedTab==2}">
          {{variations.length}} Remixes
        </button>
      </div>

      <div id="empty-list-image-block" v-if="!isReplication()">
        <div v-if="!hasReplications() && selectedTab==1">
          <img class="empty-list-image" src="@/assets/arabica-18@2x.png"/>
          <p v-if="finding.UserId==userId">Replicate your inquiry to validate it</p>
          <p v-else>Be the first to replicate this!</p>
        </div>
        <div v-if="!hasRemixes() && selectedTab==2">
          <img class="empty-list-image" src="@/assets/arabica-upgrade@2x.png"/>
          <p>No remixes yet</p>
        </div>
      </div>

      <div id="similarity-description" v-if="isReplication()">
        <div v-if="finding.Similarity == 1">
          <span class="similar-icon"></span> 
          <label>Findings are similar to the original inquiry</label>
        </div>
        <div v-if="finding.Similarity == 2">
          <span class="similar-icon"></span> 
          <label>Findings differ from the original inquiry</label>
        </div>
        <div v-if="finding.Similarity == 3">
          <span class="similar-icon"></span> 
          <label>Not sure how findings compare to the original</label>
        </div>
      </div>

      <div id="similarity-figures" v-if="selectedTab==1 && !finding.Base && hasReplications()">
        <div class="similar-block" style="margin-right: 8px;">
          <span class="similar-icon"></span>
          <span><label class="similar-value">{{similarResults}}</label></span>
          <br><span class="similar-label">Similar</span>
        </div>
        <div class="similar-block" style="margin-right: 8px;">
          <span class="similar-icon"></span>
          <span><label class="similar-value">{{differentResults}}</label></span>
          <br><span class="similar-label">Different</span>
        </div>
        <div class="similar-block">
          <span class="similar-icon"></span>
          <span><label class="similar-value">{{notsureResults}}</label></span>
          <br><span class="similar-label">Not sure</span>          
        </div>
      </div>
     
      <button v-if="isOriginal() || isRemix()" class="k-button-1" @click="replicateOrRemix()"> 
        {{selectedTab==1? 'Replicate': 'Remix'}} 
      </button>

      <div class="invite-others-section" v-if="!isReplication() && !hasReplications() && isOwnFinding()"> 
        <p>Invite others to view your inquiry</p>
        <div class="link-invite-button" @click="copyLink()"> 
          <img src="@/assets/insert-link-24-px@2x.png"/> <label>{{copyLinkText}}</label>
        </div>
        <div class="link-invite-button" @click="inviteOthers()"> 
          <img src="@/assets/mail-24-px@2x.png"/> <label>Send Invite</label>
        </div>
      </div>
      <!-- <div v-if="linkCopied" style="background-color: #FFEBE3; padding-top: 10px; padding-bottom: 10px; border-radius: 10px; font-weight: bold; color: #FF5B24">Link copied</div> -->

      <div id="replications-block" class="finding-card-list" v-if="selectedTab==1 && isOriginal()">
        <el-card v-for="replication in replications" v-bind:key="replication.id" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(replication.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(replication)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{replication.UserNickname}}</div>
            <div class="box-name">{{replication.Name}}</div>
          </div>
        </el-card>
      </div>

      <div id="original-finding-block" class="finding-card-list" v-if="isReplication()">
        <el-card style="margin-bottom: 20px;" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(parentFinding.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(parentFinding)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{parentFinding.UserNickname}}</div>
            <div class="box-name">{{parentFinding.Name}}</div>
          </div>
        </el-card>
      </div>

      <div id="variations-block" class="finding-card-list" v-if="selectedTab==2">
        <el-card v-for="variation in variations" v-bind:key="variation.id" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(variation.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(variation)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{variation.UserNickname}}</div>
            <div class="box-name">{{variation.Name}}</div>
          </div>
        </el-card>
        
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL,
  })
  export default {
    data() {
      return {
        username:"",
        finding:{Name:"", Description:"", Dataset:[{Media:""}], Type:1, Base:null, Status:2},
        parentFinding:{Name:"", Description:"", Dataset:[{Media:""}], Type:0, Base:"_"},
        comments: [],
        replications: [],
        similarResults: 0,
        differentResults: 0,
        notsureResults: 0,
        variations: [],
        selectedTab: 1,
        isCommentInputEnabled: false,
        commentValue: "",
        commentType: 0,
        datasetIndex: 0,
        userId: "",
        showPublishMenu: false,
        selectedComment: null,
        originalComment: {value:"", type:0},
        commentToDelete: "",
        interceptors: 0,
        showLoading: false,
        linkCopied:false,
        copyLinkText: "Copy Link",
      }
    },
    props: ['id'],
    // beforeCreate(){
    //   this.$loading.show({delay:0, background: '#FFFFFF'});  
    // },
    created() {
      console.log(process.env.VUE_APP_URL);
      var findingId = sessionStorage.findingId;
      if(this.id){
        findingId = this.id;
      }
      this.username = sessionStorage.name;
      this.userId = sessionStorage.uid;

      this.interceptors++;
      api.get('/finding/'+findingId).then((response)=> {
        this.interceptors--;
        this.$loading.hide();
        this.finding = response.data;
        if(this.finding.Base){
          api.get('/finding/'+this.finding.Base).then((response)=> {
            this.parentFinding = response.data;
            console.log(this.parentFinding);
          });
        }
      }).catch((error)=>{
        this.interceptors--; 
        alert(error.message);
      });
      this.interceptors++;
      api.get('/comment/finding/'+findingId).then((response) => {
        this.interceptors--;
        this.comments = response.data;
      });

      this.interceptors++;
      api.get('/finding/replication/'+findingId).then((response)=>{
        this.interceptors--;
        this.replications = response.data;
        this.calculateSimilarities();
      }).catch((error)=>{
        this.interceptors--; 
        alert(error.message);
      });

      this.interceptors++;
      api.get('/finding/variation/'+findingId).then((response)=>{
        this.interceptors--;
        this.variations = response.data;
      }).catch((error)=>{
        this.interceptors--; 
        alert(error.message);
      });

    },
    methods: {
      goBack() {
        this.$router.go(-1)
      },
      enableCommentInput(type){
        if(this.selectedComment){
          return;
        }
        this.isCommentInputEnabled = true;
        this.commentType = type;
      },
      postComment(){
        const url = '/comment/';
        const comment = {
          userId : sessionStorage.uid,
          findingId: sessionStorage.findingId,
          value: this.commentValue,
          type: this.commentType
        }
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.post(url, qs.stringify(comment),config).then((response)=>{
          var savedComment = response.data;
          this.isCommentInputEnabled = false;
          this.commentType = 0;
          this.commentValue = "";
          this.comments.splice(0,0,response.data);
        });
      },
      getMedia(finding){
        if(finding.Dataset.length<this.datasetIndex){
          return "default-finding-image.jpg";
        }
        if(!finding.Dataset[this.datasetIndex]){
          return "default-finding-image.jpg";
        }
        if(finding.Dataset[this.datasetIndex].Media == null || finding.Dataset[this.datasetIndex].Media == ""){
          return "default-finding-image.jpg";
        }
        return finding.Dataset[this.datasetIndex].Media+'.jpg';
      },
      replicateOrRemix(){
        var newFinding = this.finding;
        if(this.selectedTab==1){
          newFinding.Type = 2;
        } else {
          newFinding.Type = 3;
          newFinding.Dataset.Condition = "";
        }
        newFinding.Dataset.Uv = 0;
        newFinding.Dataset.Lux = 0;
        newFinding.Dataset.Media = "";
        newFinding.Base = this.finding.Id;
        newFinding.Id = null;
        newFinding.UserId = sessionStorage.uid;
        this.$store.dispatch('setFindingData', newFinding);
        this.$router.push({path: '/find_ar'});
      },
      calculateSimilarities(){
        this.similarResults = 0;
        this.differentResults = 0;
        this.notsureResults = 0;
        this.replications.forEach((r)=>{
          if(r.Similarity == 1){
            this.similarResults++;
          } else if(r.Similarity == 2){
            this.differentResults++;
          } else {
            this.notsureResults++;
          }
        })
      },
      loadFinding(findingId){
        console.log("load finding...");
        this.$store.dispatch('setFindingIdData', findingId);
        window.location.reload(); //find the vue method to reload component
      },
      setDatasetIndex(value){
        if(this.finding.Dataset.length==0){return;}
        if(this.datasetIndex+value<0){
          return;
        }
        if(this.datasetIndex+value == this.finding.Dataset.length){
          return;
        }
        this.datasetIndex += value;
        this.reload();
      },
      reload() {
        this.$forceUpdate();
      },
      jump(url) {
        this.$router.push({path: '/find_ar'})
      },
      unpublish(){
        this.showPublishMenu = false;
        const url = '/finding/' + this.finding.Id;
        this.finding.Status = 1;
        console.log(this.finding);
        api.put(url, this.finding).then((response)=>{
          this.$router.push({path: '/home/myinquiries'});
        });
      },
      copyLink(){
        this.showPublishMenu = false;
        var findingLink = "https://learn.kiwrious.com/#/login/"+this.finding.Id;
        navigator.clipboard.writeText(findingLink);
        this.linkCopied = true;
        this.copyLinkText = "Copied";
        // window.setTimeout(()=>{this.linkCopied=false;}, 3000);
      },
      inviteOthers(){
        this.showPublishMenu = false;
        var subject = this.username+"'s Kiwrious Inquiry";
        var encodedSubject = encodeURIComponent(subject);
        var findingLink = "https://learn.kiwrious.com/#/login/"+this.finding.Id;
        var body = "Check out my new Kiwrious Inquiry: " + findingLink + " \nI'm inviting you to comment on, replicate or remix it!";
        var encodedBody = encodeURIComponent(body);
        document.location.href = "mailto:?subject="+encodedSubject+"&body="+encodedBody;
      },
      deleteFinding(){
        this.$dialog({content: 'Permanently delete this inquiry? You can\'t undo this',
          btns: [{label: 'Cancel',color: '#444',ghost: true}, {label: 'Yes',color: 'gray',callback: this.onConfirmDeleteFinding}]
        });
      },
      onConfirmDeleteFinding(){
        this.showPublishMenu = false;
        const url = '/finding/' + this.finding.Id;
        api.delete(url).then((response)=>{
          this.$router.push({path: '/home/myinquiries'});
        });
      },
      deleteComment(commentId){
        this.commentToDelete = commentId;
        this.$dialog({content: 'Delete this comment?',
          btns: [{label: 'Cancel',color: '#444',ghost: true}, {label: 'Yes',color: 'gray',callback: this.onConfirmDeleteComment}]
        });
      },
      onConfirmDeleteComment(){
        const url = '/comment/' + this.commentToDelete;
        var deletedIndex = this.comments.indexOf(this.comments.find(c=> c.Id==this.commentToDelete));
        this.comments.splice(deletedIndex, 1);
        api.delete(url).then((response)=>{
         console.log(response);
        });
      },
      editComment(comment){
        if(this.isCommentInputEnabled){return;}
        this.selectedComment = comment;
        this.originalComment.value = comment.Value;
        this.originalComment.type = comment.Type;
      },
      revertComment(comment){
        comment.Value = this.originalComment.value;
        comment.Type=  this.originalComment.type;
        // this.selectedComment = this.originalComment;
        this.selectedComment = null;
      },
      saveComment(editedComment){
        this.selectedComment = null;
        const url = '/comment/' + editedComment.Id;
        var comment = {id:editedComment.Id, value:editedComment.Value, type:editedComment.Type}
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.put(url, qs.stringify(comment),config).then((response)=>{
          
        });
      },
      isOriginal(){
        return this.finding.Type==1;
      },
      isRemix(){
        return this.finding.Type==3;
      },
      isReplication(){
        return this.finding.Type==2;
      },
      hasReplications(){
        return this.replications.length>0;
      },
      hasRemixes(){
        return this.variations.length>0;
      },
      isOwnFinding(){
        return this.finding.UserId==this.userId;
      },
      openDialog(method){
          this.$dialog({title: 'Confirm deletion', content: 'Do you want to delete this?',
          btns: [{label: 'Yes',color: '#09f',callback: method},{label: 'Cancel',color: '#444',ghost: true}]
      });
      },
      getSceneNotes(){
        if(this.finding && this.finding.Dataset && this.finding.Dataset[this.datasetIndex]){
          return this.finding.Dataset[this.datasetIndex].Notes;
        }
        return "";
      }
    },
    watch: {
      interceptors(){
        if(this.interceptors>0 && !this.showLoading){
         this.$loading.show({delay:0, background: '#FFFFFF'});  
         this.showLoading = true;
        } else {
          this.showLoading = false;
          this.$loading.hide();
        }
      }
    }
  }
</script>

<style lang="scss">
  @font-face{
    font-family: 'roboto-regular';
    src: url('../assets/fonts/Roboto-Regular.ttf');
  }
  body {
    font-family: "roboto-regular";
  }
  .finding_wrap {
    // filter: blur(3.5rem);
    padding-right: 30px;
    p {
        font-size: 20px;
        font-weight: 600;
      }
  }
  .finding_content {
    display: inline-block;
    width: 60%;
    text-align: left; 
    i {
      font-size: 3vh;
      font-weight: 600;
    }
    img {
      width: 100%;
      height: auto;
    }
    .finding_block {
      position: relative;
      margin-right: 30px;
      margin-bottom: 20px;
      background-color: #E4EEEF;
      border-radius: 5px;
      .finding-title{
        background-color: white;
        #finding-name {
          margin:0;
          padding: 20px;
          padding-left: 0;
          padding-bottom: 10px;
          color: #fa5708;
          font-size: 4.5vh;
        }
        #finding-description{
          margin:0;
          padding: 0 20px 20px 0;
          font-weight: normal;
        }
      }
      .notes-section{
        display: block;
        width: 100%;
        min-height: 100px;
        margin-top: 0;
        border-radius: 0 0 10px 10px;
        #finding-time{
          margin: 0;
          padding: 20px;
          padding-bottom: 10px;
          color: #005666;
          font-size: 2.5vh;
          font-weight: normal;
        }
        #finding-note{
          padding: 0 20px 20px 20px;
          margin: 0;
          font-size: 2.5vh;
          font-weight: normal;
        }
      }
    }
    #finding_comment {
      margin-right: 30px;
      padding: 30px;
      padding-top: 10px;
      background-color: #F3F5F5;
      border-radius: 5px;
      .comment_row {
        background-color: white;
        margin-bottom: 15px;
        padding: 16px;
        border-radius: 5px;
        .comment-user{
          font-size: 2.5vh;
        }
        .comment-value{
          font-size: 2.5vh;
        }
        input {
          margin-top: 5px;
          margin-right: 10px;
        }
        button {
          background-color: #FF5B24;
          color: white;
          font-size: 16px;
          height: 28px;
          width: auto;
          padding-left: 10px;
          padding-right: 10px;
          margin-left: 10px;
          vertical-align: top;
          border-radius: 20px !important;
          border: 1px solid orange;
          outline: none;
          cursor: pointer;
        }
        i {
          color: grey;
        }
        img {
          vertical-align: top;
          width: 3.5vh;
          height: 3.5vh;
          margin-right: 10px;
        }
        // span {
        //   vertical-align: middle;
        // }
