<template>
    <div>
        <div style="width: 50%; height: 100px; text-align: left; display: inline-block;">
            <input @keyup="filterResults()" v-model="searchQuarry" placeholder="Search challenges" style="margin-left: 65px; vertical-align: top;" class="k-input-1" type="text" id="fname" name="fname">
        </div>
        <div style="width: 50%; height: 100px; text-align: right; display: inline-block;">
            <el-button style="padding: 10px;" round @click="filterBySensor('')">
                All
            </el-button>
            <el-button round @click="filterBySensor('uv')"> <img src="@/assets/sensor-uv@2x.png" width="40" height="40"/></el-button>
            <el-button round @click="filterBySensor('con')"><img src="@/assets/sensor-conductance@2x.png" width="40" height="40"/></el-button>
            <el-button round @click="filterBySensor('voc')"><img src="@/assets/sensor-voc@2x.png" width="40" height="40"/></el-button>
            <el-button round @click="filterBySensor('tem')"><img src="@/assets/sensor-humidity@2x.png" width="40" height="40"/></el-button>
            <el-button round @click="filterBySensor('col')"><img src="@/assets/sensor-colour@2x.png" width="40" height="40"/></el-button>
        </div>
        <el-row id="all-findings-list" v-if="!searchQuarry">
            <el-col :span="6" v-for="challenge in challenges" :offset="1" v-bind:key="challenge.Id">
                <el-card style="position: relative;" class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(challenge.Id)">
                <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(challenge)">
                <!-- <el-button v-if="finding.Status==1" class="draft-button" style="position: absolute; top: 10px; left: 10px;">DRAFT</el-button> -->
                <div class='box-description'>
                    <div class="box-user"><i class="el-icon-user-solid"></i>{{challenge.UserNickname}}</div>
                    <div style="display: inline-block; width: 12%; text-align: right; font-size: 30px; font-weight: bold;">&#8942;</div>
                    <div class="box-name">{{challenge.Name}}</div>
                </div>
                </el-card>
            </el-col>
        </el-row>
        <el-row id="filtered-findings" v-if="searchQuarry">
            <el-col :span="6" v-for="challenge in filtered" :offset="1" v-bind:key="challenge.Id">
                <el-card style="position: relative;" class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(challenge.Id)">
                <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(challenge)">
                <!-- <el-button v-if="finding.Status==1" class="draft-button" style="position: absolute; top: 10px; left: 10px;">DRAFT</el-button> -->
                <div class='box-description'>
                    <div class="box-user"><i class="el-icon-user-solid"></i>{{challenge.UserNickname}}</div>
                    <div style="display: inline-block; width: 12%; text-align: right; font-size: 30px; font-weight: bold;">&#8942;</div>
                    <div class="box-name">{{challenge.Name}}</div>
                </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL,
  })
export default {
    data(){
        return  {
            allchallenges: null,
            challenges: null,
            searchQuarry: "",
            filtered: null,
        }
    },
    created(){
        this.$root.selectedPage = "Challenges";
        api.get('/challenge/').then((response)=> {
        // this.interceptors--;
        // this.$loading.hide();
        this.challenges = response.data;
        this.allchallenges = response.data;

        console.log(this.challenges);
        // if(this.finding.Base){
        //   api.get('/finding/'+this.finding.Base).then((response)=> {
        //     this.parentFinding = response.data;
        //     console.log(this.parentFinding);
        //   });
        // }
      }).catch((error)=>{
        // this.interceptors--; 
        alert(error.message);
      });
    },
    methods:{
        jump(url) {
            this.$router.push({path: url})
        },
        toFinding(fid) {
            debugger;
            this.$store.dispatch('setFindingIdData', fid)
            this.$router.push({path: '/view_challenge'})
        },
        getMedia(finding){
            if(finding.Dataset[0].Media == null){
            return "default-finding-image.jpg";
            }
            return finding.Dataset[0].Media+'.jpg';
        },
        filterResults(){
            this.filtered = this.challenges.filter(finding=>finding.Name.toLowerCase().includes(this.searchQuarry.toLowerCase()) || finding.UserNickname.toLowerCase().includes(this.searchQuarry.toLowerCase()));
        },
        filterBySensor(sensorName){
            if(!sensorName){
                this.challenges = this.allchallenges;
                return;
            }
            this.challenges = this.allchallenges.filter(c=>c.SensorId.includes(sensorName));
        }
    }
}
</script>

<style scoped>
    .el-button {
        vertical-align: top;
        border: none;
        background-color: #F4F7FA !important;
        padding: 10px !important;
        height: 60px !important;
        width: 80px !important;
        font-size: 20px;
    }
    .el-button:hover {
        background-color: #FFEEE7  !important;
    }
    .el-button:focus {
        background-color: #FFEEE7 !important;
    }
</style>