<template>
  <div id="find_ar">
    <img id="ar-clear" class="clickable" src="@/assets/clear-24-px.png" v-on:click="exit()">
    <div id="challenge-card" v-show="challenge_status && sensor_connect">
      <div>Challenge Goal</div>
      <p>{{challenge_condition}}</p>
      <p>{{challenge_value}}</p>
    </div>
    <div id="ar-wrapper">
      <div id="video-wrapper">
        <div id="video-container">
          <!-- Sensor Connect -->
          <img src="@/assets/img-connect-sensor.png" v-show="!sensor_connect">
          <p v-show="!sensor_connect">Please plug in sensor and click Connect</p>
          <serial v-show="!sensor_connect" @nuvData="nuvData" @nluxData="nluxData" @nsensor_connected="nsensor_connected"></serial>

          <!-- Don't Move Canvas! -->
          <video id="sourcevideo" class="layer" playsinline v-show="sensor_connect"></video>
          <canvas id="targetcanvas" class="layer" v-show="sensor_connect"></canvas>        
        </div>
        <div id="video-layer" ref="print" v-show="sensor_connect">
          <uvluxhex v-show="sensor_connect" />
          <!-- <img id="detect-img" src="@/assets/uv-sensor-image.png" v-show="sensor_connect && !marker_detect_first"> -->
          <el-popover
          ref="input_label"
          placement="bottom"
          width="200"
          trigger="manual"
          v-model="lable_tip"
          >
            Help others understand<br/> what youre testing in this<br/> scene with a clear label.
          </el-popover>
          <input  type="text" name="" v-popover:input_label v-model="img_label" @focus="lable_tip = true" @blur="lable_tip = false" v-show="sensor_connect" placeholder="Label this Scene" :readonly="read">
          <img id="img-check" class="layer" :src="reimg_src" v-show="img_visible">
        </div>
      </div>
      <div id="function-bar" v-show="sensor_connect">
        <div id="camera-function">
          <button class="k-button-1" @click="switchCamera()">
            <img :src="cameraImg">
          </button>
        </div>
        <div id="snap-retake">
          <img id="snap_button" :src="snap_button" @click="snap()" v-show="snap_visible">
          <button class="k-button-1" v-show="!snap_visible" @click="deleteImg()">Delete</button>
        </div>
        <div id="cancel-go">
          <img id="img_done" src="@/assets/ar_done.png" @click="goOverview()" v-show="next && snap_visible">
          <button class="k-button-1" v-show="!snap_visible" @click="backSnap()">Cancel</button>  
        </div>
        
        <canvas id="snap_img" width="640" height="400" style="display:none;"></canvas>
      </div>
      <div id="img-preview" v-show="sensor_connect">
        <img :src="img1" @click="checkImg(0)" :class="{'active_img': reimg_index == 0}">
        <img :src="img2" @click="checkImg(1)" :class="{'active_img': reimg_index == 1}">
        <img :src="img3" @click="checkImg(2)" :class="{'active_img': reimg_index == 2}">
      </div>
    </div>
    <div id="scene-notes" v-show="sensor_connect">
      <span>{{time}}</span>
      <el-input
        type="textarea" v-model="img_note" placeholder="Add notes to help others recreate this scene accurately when replicating your inquiry." :readonly="read" maxlength="200" show-word-limit></el-input>
    </div>
  </div>
</template>

<script>
  // import ARToolkit from 'artoolkit5-js'
  // import * as THREE from 'three'
  import serial from '@/components/getSerial'
  import uvluxhex from '@/components/uv-lux-plain'

  let sourceVideo;
let targetCanvas;

// AR controller reference
let arc;

// internal HIRO marker ID
let markerId;

// used to render THREE.js scene
let renderer, scene, camera, markerRoot;

export default {
  data() {
    return {
      sensor_connect: false,
      marker_detect: false,
      marker_detect_first: false,
      uvData: null,
      luxData: null,
      uvSave: null,
      luxSave: null,
      count: null,
      output: null,
      snap_button: 'snap_inactive.png',
      img_label: null,
      img_note:null,
      print_src: null,
      reimg_index: null,
      reimg_src: null,
      img1: null,
      img2: null,
      img3: null,
      img4: null,
      img5: null,
      ar_img: [],
      ar_label: [],
      ar_note: [],
      next: false,
      time: null,
      ar_re: this.$store.state.finding,
      read: false,
      lable_tip: false,
      img_visible: false,
      snap_visible: true,
      video_status: false,
      cameraImg: 'camera-on.svg',
      challenge_status: false,
      challenge_condition: null,
      challenge_value: null,
    }
  },
  components: {
    'serial': serial,
    'uvluxhex': uvluxhex,
  },
  watch: {
    img_label() {
      if (this.img_label != null && this.img_label != '') {
        this.snap_button = 'snap_active.png'
      }    
    },
    img1() {
      if (this.img1 != null) {
        this.next = true
      }
    },
    sensor_connect() {
      if (this.sensor_connect == true) {
        this.updateValue()
        const video_background = document.querySelector('#video-container')
        video_background.style.backgroundColor = '#00414d'
      }
    }
  },
  created() {
    console.log(this.ar_re);
    if (sessionStorage.connection == 'true') {
      this.sensor_connect = true
      console.log('22')
    }
    if (this.ar_re != null) {
      const type_base = {
        type: this.ar_re.Type,
        base: this.ar_re.Base
      }
      this.$store.dispatch('setArreData', type_base)
      this.img_label = this.ar_re.Dataset[0].Condition
      this.img_note = this.ar_re.Dataset[0].Notes
      if (this.ar_re.Type == '2') {
        this.read = true
      } else if (this.ar_re.Type == '4') {
        this.setChallengecard()
      }
    } else {
      const type_base = {
        type: 1,
        base: null
      }
      this.$store.dispatch('setArreData', type_base)
    }
  },
  mounted() {
    const date = new Date()
    const options = {  year: 'numeric', month: 'long', day: 'numeric', hour: '2-digit', minute: '2-digit', timeZoneName:'short'};
    this.time = date.toLocaleString('en-NZ', options)
    if (this.$route.query.editIndex == 0) {
      this.ar_img = this.$store.state.arimg
      this.ar_label = this.$store.state.arlabel
      this.ar_note = this.$store.state.arnote
      this.img1 = this.ar_img[0]
      this.img2 = this.ar_img[1]
      this.img3 = this.ar_img[2]
      this.checkImg(0)
    }
  },
  methods: {
    nuvData(uv) {
      this.uvData = uv
      const value = document.getElementById('value-uv')
      value.textContent = uv
    },
    nluxData(lux) {
      this.luxData = lux
      const value = document.getElementById('value-lux')
      value.textContent = lux
    },
    nsensor_connected(connect) {
      this.sensor_connect = connect
    },
    exit() {
      this.$store.dispatch('setFindingData', null)
      this.$router.push({path: '/home/myinquiries'})
    },
    setChallengecard() {
      this.challenge_status = true
      this.challenge_condition = this.ar_re.Dataset[0].Condition
      this.challenge_value = this.ar_re.Dataset[0].Uv
    },
    switchCamera() {
      if (this.video_status == false) {
      this.initCamera()
      .then(video => {
        // start camera playback
        sourceVideo = video;
        sourceVideo.width = 640;
        sourceVideo.height = 400;
        sourceVideo.play();
        return new Promise(resolve => {
          sourceVideo.addEventListener("loadeddata", event => {
            console.log("Camera is ready");
            resolve();
          });
        })
        })
      this.video_status = true
      this.cameraImg = 'camera-off.svg'
      } else {
        const video = document.querySelector("#sourcevideo");
        const stream = video.srcObject
        stream.getTracks().forEach(function(track) {
          track.stop();
        });
        video.srcObject = null
        this.video_status = false
        this.cameraImg = 'camera-on.svg'
      }
    },
    goOverview() {
      console.log('here')
      this.$store.dispatch('setArimgData', this.ar_img)
      this.$store.dispatch('setArnoteData', this.ar_note)
      this.$store.dispatch('setArlabelData', this.ar_label)
      this.$store.dispatch('setTimeData', this.time)
      this.$store.dispatch('setUVData', this.uvSave)
      this.$store.dispatch('setLUXData', this.luxSave)
      this.$store.dispatch('setFindingData', null)
      this.$router.push({path: '/overview'})
    },
    checkImg(index) {
      if (this.ar_img[index] != null) {
        this.reimg_src = this.ar_img[index]
        this.reimg_index = index
        this.snap_visible = false
        this.img_visible = true
        if (this.img2 == null) {
          this.img2 = 'empty_img.png'
          this.img3 = 'empty_img.png'
        } else if (this.img3 == null) {
          this.img3 = 'empty_img.png'
        }
        this.img_note = this.ar_note[index]
        this.read = true
      } else {
        this.backSnap()
      }
    },
    snap() {
      if (this.snap_button == 'snap_active.png') {
        this.uvSave = this.uvData
        this.luxSave = this.luxData
        let vm = this
        const snap_img = document.querySelector('#snap_img')
        const ctx = snap_img.getContext('2d')
        ctx.clearRect(0,0,640,400);
        let background = null
        if (this.video_status == true) {
          background = document.querySelector('#sourcevideo')
          ctx.drawImage(background, 0, 0, 640, 400)
        } else {
          ctx.fillStyle="#00414d";  
          ctx.beginPath();  
          ctx.fillRect(0,0,640,400);  
          ctx.closePath();
        }
        let input_image = new Image()

        this.print()
        .then(() => {
          input_image.src = this.print_src
          console.log(input_image.src)
          input_image.onload = function() {
            // ctx.drawImage(input_image, 217, 330, 206, 46)
            ctx.drawImage(input_image, 0,0,640,400)
            ctx.fillStyle = "white"
            ctx.font = '20px arial'
            ctx.textAlign = 'center'
            ctx.textBaseline = 'middle'
            ctx.fillText(vm.img_label, 320, 352)
            if (vm.img1 == null) {
              vm.img1 = snap_img.toDataURL('image/jpg', 1)
              vm.ar_img[0] = vm.img1
              vm.ar_label[0] = vm.img_label
              vm.ar_note[0] = vm.img_note
            } else if (vm.img2 == null) {
              vm.img2 = snap_img.toDataURL('image/jpg', 1)
              vm.ar_img[1] = vm.img2
              vm.ar_label[1] = vm.img_label
              vm.ar_note[1] = vm.img_note
            } else {
              vm.img3 = snap_img.toDataURL('image/jpg', 1)
              vm.ar_img[2] = vm.img3
              vm.ar_label[2] = vm.img_label
              vm.ar_note[2] = vm.img_note
            }
            vm.img_label = null
            vm.img_note = null
          }
        })
      } else {
        this.lable_tip = true
      }
      // console.log(image)
    },
    deleteImg() {
      this.ar_img.splice(this.reimg_index, 1)
      this.ar_label.splice(this.reimg_index, 1)
      this.ar_note.splice(this.reimg_index, 1)
      this.img1 = this.ar_img[0]
      this.img2 = this.ar_img[1]
      this.img3 = this.ar_img[2]
      this.img_visible = false
      this.snap_visible = true
      this.reimg_index = null
      this.read = false
    },
    backSnap() {
      this.img_visible = false
      this.snap_visible = true
      this.reimg_index = null
      this.img_note = null
      this.read = false
      if (this.img2 == 'empty_img.png') {
          this.img2 = null
        }
        if (this.img3 == 'empty_img.png') {
          this.img3 = null
        }
    },
    async print() {
      const el = this.$refs.print;
      // add option type to get the image version
      // if not provided the promise will return 
      // the canvas.
      const options = {
        type: 'dataURL',
        backgroundColor: null,
      }
      this.print_src = await this.$html2canvas(el, options);
    },
    updateValue() {
      const value = document.getElementById('value-uv')
      
    },
    showData() {
      let data_canvas = document.querySelector('#uvcanvas')
      let ctx = data_canvas.getContext('2d')
      ctx.beginPath()
      ctx.arc(64, 64, 64, 0, Math.PI*2, true);
      ctx.fillStyle = 'white'
      ctx.closePath()
      ctx.fill()
      ctx.fillStyle = "black"
      ctx.font = '24px arial'
      ctx.textBaseline = 'middle'
      this.uvSave = this.uvData
      ctx.fillText(this.uvData, 52, 60)
      ctx.fillStyle = "black"
      ctx.font = '16px arial'
      ctx.textBaseline = 'middle'
      ctx.fillText('UVI', 52, 80)

      data_canvas = document.querySelector('#luxcanvas')
      ctx = data_canvas.getContext('2d')
      ctx.beginPath()
      ctx.arc(64, 64, 64, 0, Math.PI*2, true);
      ctx.fillStyle = 'white'
      ctx.closePath()
      ctx.fill()
      ctx.fillStyle = "black"
      ctx.font = '24px arial'
      ctx.textBaseline = 'middle'
      this.luxSave = this.luxData
      ctx.fillText(this.luxData, 52, 60)
      ctx.fillStyle = "black"
      ctx.font = '16px arial'
      ctx.textBaseline = 'middle'
      ctx.fillText('LUX', 52, 80)
    },
    updateData() {
      this.showData()
      let uvdataMap = new THREE.CanvasTexture(document.querySelector('#uvcanvas'))
      let uvdataMaterial = new THREE.SpriteMaterial( { map: uvdataMap } );
      let uvdataSprite = new THREE.Sprite(uvdataMaterial)

      let luxdataMap = new THREE.CanvasTexture(document.querySelector('#luxcanvas'))
      let luxdataMaterial = new THREE.SpriteMaterial( { map: luxdataMap } );
      let luxdataSprite = new THREE.Sprite(luxdataMaterial)
      luxdataSprite.position.x = 1
      luxdataSprite.position.y = 1

      return [uvdataSprite, luxdataSprite]
    },
    async initCamera() {
      const constraints = {
        audio: false,
        video: {
          //facingMode: "environment",
          facingMode: "user",
          width: 640,
          height: 400,
          frameRate: { max: 30 }
        }
      };

      // initialize video source
      const video = document.querySelector("#sourcevideo");
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      video.srcObject = stream;

      return new Promise(resolve => {
        video.onloadedmetadata = () => {
          resolve(video);
        };
      });
    },
    async initAR() {
      // init AR controller
      // Note: this camera_para.dat file works well for most built-in laptop webcams
      // It does NOT work very well for newer iPhone models (X / XS / 11)
      // The cube will be off quite a bit.
      arc = await ARToolkit.ARController.initWithDimensions(
        640, 400,
        '/data/ar/camera_para.dat'
      );
      console.log("AR Controller initialized");

      // add marker
      markerId = await arc.artoolkit.addMarker(arc.id, '/data/ar/pattern-sun.patt');
      console.log("HIRO marker added with marker ID", markerId);
    },
    initTargetCanvas() {
      // target canvas should overlap source video
      targetCanvas = document.querySelector("#targetcanvas");
      targetCanvas.width = sourceVideo.width;
      targetCanvas.height = sourceVideo.height;
    },
    initRenderer() {
      // create a scene overlaying the video
      renderer = new THREE.WebGLRenderer({ canvas: targetCanvas, alpha: true });
      renderer.setSize(640, 400);
      renderer.setClearColor(0xffffff, 0);
      renderer.autoClear = false;
      // init camera
      camera = new THREE.Camera();
      camera.matrixAutoUpdate = false;
      scene = new THREE.Scene();
      scene.add(camera);

      markerRoot = new THREE.Group()
      const uvdataSprite  = this.updateData()[0]
      const luxdataSprite  = this.updateData()[1]
      const spriteMap = new THREE.TextureLoader().load( "/data/ar/happy_zally.png" );
      const spriteMaterial = new THREE.SpriteMaterial( { map: spriteMap } );
      const sprite = new THREE.Sprite( spriteMaterial );
      sprite.position.z = 0.5
      sprite.position.x = -1
      sprite.position.y = -1
  
      markerRoot.add(sprite)
      markerRoot.add(uvdataSprite)
      markerRoot.add(luxdataSprite)  
      markerRoot.markerMatrix = new Float64Array(12);
      markerRoot.matrixAutoUpdate = false;
      console.log(markerRoot)
      scene.add(markerRoot)
    },

    startProcessing() {
      const processFrame = () => {
        const result = arc.detectMarker(sourceVideo);
        if(result !== 0) {
          // ARToolkit returning a value !== 0 means an error occured
          console.log('Error detecting markers');
          return;
        }
        // get the total number of detected markers in frame
        const markerNum = arc.getMarkerNum();
        let hiroMarkerNum = false;
        // check if one of the detected markers is the HIRO marker
        for(let i = 0; i < markerNum; i++) {
          const markerInfo = arc.getMarker(i);
          if(markerInfo.idPatt == markerId) {
            // store the marker ID from the detection result
            hiroMarkerNum = i;
            break;
          }
        }

        if(hiroMarkerNum !== false) {
          // HIRO marker found
          if(markerRoot.visible) {
            arc.getTransMatSquareCont(
              hiroMarkerNum, 1, markerRoot.markerMatrix, markerRoot.markerMatrix
            );
          } else {
            arc.getTransMatSquare(
              hiroMarkerNum , 1 /* Marker width */, markerRoot.markerMatrix
            );
          }
          // show marker root
          markerRoot.remove(uvdataSprite)
          markerRoot.remove(luxdataSprite)
          const uvdataSprite = this.updateData()[0]
          const luxdataSprite = this.updateData()[1]
          markerRoot.add(uvdataSprite)
          markerRoot.add(luxdataSprite)
          this.marker_detect = true;
          this.marker_detect_first = true
          markerRoot.visible = true;

          // position camera
          arc.arglCameraViewRHf(
            arc.transMatToGLMat(markerRoot.markerMatrix),
            markerRoot.matrix.elements,
            0.5
          );
        } else {
          // not found
          markerRoot.visible = false;
          this.marker_detect = false;
        }
        // render the scene
        renderer.clear();
        renderer.render(scene, camera);
        // process next frame
        window.requestAnimationFrame(processFrame);
      };
      // initialize camera projection matrix
      const cameraMatrix = arc.getCameraMatrix();
      camera.projectionMatrix.fromArray(cameraMatrix);
      processFrame();
    },
  }
}
</script>

<style>
/*svg {
  position: absolute;
}
*/
#challenge-card {
  width: 248px;
    height: 470px;
    position: absolute;
    left: 5%;
    top: 72px;
    border-radius: 10px;
    border: 2px solid #323335;
}
#ar-clear {
  float: right;
  margin-top: 20px;
  margin-right: 5%;
}
#ar-wrapper {
  width: 640px;
  height: 582px;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
#ar-wrapper svg {
  position: absolute;
  margin-top: 100px;
  left: 160px;
}
#video-wrapper {
  width: 640px;
  height: 400px;
}
  #video-container {
  width: 640px;
  height: 400px;
  margin: auto;
  left: 0;
  right: 0;
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
  position: absolute;
}

.layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

#sourcevideo {
  object-fit: cover;
  /*transform: rotateY(180deg);*/
}
#video-layer {
  width: 640px;
  height: 400px;
}
#video-layer input {
  position: absolute;
  margin-top: 330px;
  left: 210px;
  height: 42px;
  width: 200px;
  border-radius: 10px;
  background-color: #005766;
  outline: none;
  text-align: center;
  border: 0px;
  font-size: 20px;
  color: #FFFFFF;
}
#video-layer input:hover {
  background-color: #006c80;
}
#video-layer input:active {
  background-color: #006c80;
}
#detect-img {
  position: absolute;
  top: 0;
  left: 0;
}
#original-data {
  position: absolute;
    width: 200px;
    background: #DDF7F5;
    margin-left: 440px;
  top: 0;
}
#function-bar {
  height: 72px;
  background-color: black;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  text-align: center
}
#camera-function {
  width: 110px;
  display: inline-block;
  float: left;
  padding-top: 8px;
}
#camera-function button {
  width: 48px;
  padding-top: 4px;
}
#snap-retake {
  width: 110px;
  margin: auto;
  padding-top: 8px;
  display: inline-block;
}
#snap_button {
  width: 42px;
  margin-top: 7px;
  cursor: pointer;
}
#cancel-go {
  width: 110px;
  display: inline-block;
  float: right;
  padding-top: 8px;
  height: 72px;
}
#cancel-go button {
  background-color: transparent;
}
#img_done {
  width: 26px;
  margin-right: 30px;
  float: right;
  margin-top: 15px;
  cursor: pointer;
}
.el-popover {
  font-size: 16px;
  color: #323335;
  font-family: 'Arial';
  text-align: center;
}
#img-preview {
  height: 110px;
  width: 640px;
  border-radius: 10px;
  background-color: #d9dce2;
}
.active_img {
  border: 2px solid #f85708;
  opacity: 0.5;
}
#img-preview img{
  width: 128px;
  margin-top: 15px; 
  float: left;
  margin-left: 15px;
  height: 80px;
}
#img-preview img:hover {
  border: 2px solid #f85708;
}
#scene-notes {
  width: 248px;
    height: 470px;
    position: absolute;
    right: 5%;
    top: 72px;
    border-radius: 10px;
    border: 2px solid #323335;
}
#scene-notes span {
  font-size: 16px;
    margin-top: 12px;
    margin-bottom: 12px;
    display: block;
}
#scene-notes textarea {
  height: 155px;
    width: 200px;
    margin-top: 12px;
    border: none;
}
</style>