<template>
	<div>
    <div style="width: 50%; height: 100px; text-align: left; display: inline-block;">
      <input @keyup="filterResults()" v-model="searchQuarry" placeholder="Search inquries" style="margin-left: 65px; vertical-align: top;" class="k-input-1" type="text" id="fname" name="fname">
    </div>
    <div style="width: 50%; height: 100px; text-align: right; display: inline-block;">
      <el-button style="padding: 10px;" round @click="filterBySensor('')">
          All
      </el-button>
      <el-button round @click="filterBySensor('uv')"> <img src="@/assets/sensor-uv@2x.png" width="40" height="40"/></el-button>
      <el-button round @click="filterBySensor('con')"><img src="@/assets/sensor-conductance@2x.png" width="40" height="40"/></el-button>
      <el-button round @click="filterBySensor('voc')"><img src="@/assets/sensor-voc@2x.png" width="40" height="40"/></el-button>
      <el-button round @click="filterBySensor('tem')"><img src="@/assets/sensor-humidity@2x.png" width="40" height="40"/></el-button>
      <el-button round @click="filterBySensor('col')"><img src="@/assets/sensor-colour@2x.png" width="40" height="40"/></el-button>
    </div>
    <el-row id="all-findings-list" v-if="!searchQuarry">
      <el-col :span="6" v-for="finding in inquiries" :offset="1" v-bind:key="finding.Id">
        <el-card class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(finding.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(finding)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{finding.UserNickname}}</div>
            <div class="box-name">{{finding.Name}}</div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <el-row id="filtered-findings" v-if="searchQuarry">
      <el-col :span="6" v-for="finding in filtered" :offset="1" v-bind:key="finding.Id">
        <el-card class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(finding.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(finding)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{finding.UserNickname}}</div>
            <div class="box-name">{{finding.Name}}</div>
          </div>
        </el-card>
      </el-col>
    </el-row>  
  </div>
</template>

<script>
	import axios from 'axios'

  var storage = [];
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL,
  })
  export default {
    data() {
      return {
        inquiries: null,
        searchQuarry: "",
        filtered: null,
        allInquiries: null
      }
    },
    beforeRouteEnter(to, from, next) {
      var organizationId = sessionStorage.oid;
      const url = '/finding/organization/'+ organizationId;//948a06f5-5f03-498a-9258-3f89ea1e2362'
      api.get(url)
      .then((response) => {
       // this.$loading.hide();
        storage = [];
        response.data.forEach(f=>{
          if(f.Type==1){
            storage.push(f);
          }
        });
        next()
      })
      .catch((error) => {
        this.$loading.hide();
        console.log(error)
        this.$message({
          message: 'Network Error, Please try again',
          type: 'error'
        })
      })
    },
    created() {
      this.$root.selectedPage = "Discover";
      this.$loading.show({delay:0, background: '#FFFFFF'});  
      this.inquiries = storage;
      this.allInquiries = storage;
    },
    methods: {
      toFinding(fid) {
        this.$store.dispatch('setFindingIdData', fid)
        this.$router.push({path: '/finding'})
      },
      getMedia(finding){
        if(finding.Dataset[0].Media == null){
          return "default-finding-image.jpg";
        }
        return finding.Dataset[0].Media+'.jpg';
      },
      filterResults(){
        this.filtered = this.inquiries.filter(finding=>finding.Name.toLowerCase().includes(this.searchQuarry.toLowerCase()) || finding.UserNickname.toLowerCase().includes(this.searchQuarry.toLowerCase()));
      },
      filterBySensor(sensorName){
          if(!sensorName){
              this.inquiries = this.allInquiries;
              return;
          }
          this.inquiries = this.allInquiries.filter(c=>c.SensorId.includes(sensorName));
      }
    },
    watch: {
       inquiries(){
         this.$loading.hide();
       }
    }
  }
</script>

<style lang="scss">
  .box-card {
    margin-bottom:20px;
    width: 110%;
    border-radius: 15px !important;
    // outline: 1px solid red;
    box-shadow: 1px 1px 1px grey;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .box-description {
      text-align: left;
      padding: 12px;
      border-radius: 10px;
      height: 80px;
      .box-user {
        font-size: 2.2vh;
        margin-bottom: 10px;
        display: inline-block;
        width: 85%;
        position: relative;
      }
      .box-name {
        margin-left: 45px;
        font-size:2.5vh;
        font-weight: bold;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        height: 30px;
      }
    }
    .el-icon-user-solid{
      font-size: 3vh !important;
    }
    .el-button {
        vertical-align: top;
        border: none !important;
        background-color: #F4F7FA !important;
        padding: 10px !important;
        height: 60px !important;
        width: 80px !important;
        font-size: 20px !important;
    }
    .el-button:hover {
        background-color: #FFEEE7  !important;
    }
    .el-button:focus {
        background-color: #FFEEE7 !important;
    }
</style>